import {action_permissions, data_permissions_groups, data_permissions_pupils} from "./possible-rights";
import {List, Switch} from "antd";

export const actionPermissions = (role, actionPermissionsForm) => {
    return action_permissions.map(actionData => {
        let default_checked = false
        role.actionPermissions.forEach(action => {
            if (action == actionData[1] && !default_checked) {
                default_checked = true
                actionPermissionsForm[actionData[0]] = actionData[1]
            }
        })
        return (
            <List.Item key={actionData[1]}>
                {actionData[0]}
                <Switch
                    defaultChecked={default_checked}
                    onChange={checked => {
                        if (checked) {
                            actionPermissionsForm[actionData[0]] = actionData[1]
                        } else {
                            delete actionPermissionsForm[actionData[0]]
                        }
                    }}
                    style={{position: "absolute", right: "15px"}}/>
            </List.Item>
        )
    })
}
export const dataPermissionsPupils = (role, dataPermissionsForm) => {
    return data_permissions_pupils.map(SeeData => {
        let default_checked = false
        for (let key in role.dataPermissions.forPupil) {
            if (key == SeeData[1] && !default_checked) {
                default_checked = true
                dataPermissionsForm.forPupil[SeeData[1]] = 1
            }
        }
        return (
            <List.Item key={SeeData[1]}>
                {SeeData[0]}
                <Switch
                    defaultChecked={default_checked}
                    onChange={checked => {
                        if (checked) {
                            dataPermissionsForm.forPupil[SeeData[1]] = 1
                        } else {
                            dataPermissionsForm.forPupil[SeeData[1]] = undefined
                        }
                    }}
                    style={{position: "absolute", right: "15px"}}/>
            </List.Item>
        )
    })
}
