import styled from "@emotion/styled";

const Div = styled.div`
  display: flex;
  justify-content: space-between;

  thead {
    background: #fff;
  }
`
const Table = styled.table`
  width: 100%;
  user-select: none;
  .week {
    td {
      text-align: left;
      background: #e6e3e3;
    }
    .white {
      background: #fff;
    }
  }
  .time {
    background: #e6e3e3;
  }
  td {
    padding: 10px;
    //box-sizing: border-box;
    width: 12.5%;
    height: 50px;
    text-align: right;
    border-top: 1px solid #f0f0f0;
    border-right: 1px solid #f0f0f0;
  }
`

export {
    Div,
    Table
}