const updateAllEmployees = (new_data) => {
    return {
        type: 'ALL_EMPLOYEES_DATA',
        payload: new_data
    }
}
const addRoles = (roles) => {
    return {
        type: 'ADD_ROLES',
        payload: roles
    }
}
const editAccount = (account) => {
    return {
        type: 'EDIT_ACCOUNT',
        payload: account
    }
}
const setButtonState = () => {
    return {
        type: 'SET_BUTTON_STATE'
    }
}

export {
    updateAllEmployees,
    addRoles,
    editAccount,
    setButtonState
}