import React, {useEffect} from 'react'
import {Table} from 'antd'
import {Box2} from "./all-employees.style";
import {updateAllEmployees} from "../../actions";
import {connect} from 'react-redux'
import {Columns} from './all-employees.logics'
import Url from './../../url/url.js'

const axios = require('axios'); // AJAX

const AllEmployees = ({data, updateAllEmployees}) => {

    const shapingUrl = (accountTypes) => {
        let str = ''
        accountTypes.forEach(item => {
        str += `&accountType=${item}`
        })
        return str
    }

    const update = (offset = 0) => {
        axios({
            method: 'get',
            url: `${Url}/AdminPanel/CRMAccounts?limit=10&offset=${offset}${shapingUrl(['admin', 'manager', 'teacher', 'senior-teacher', 'partner', 'support'])}`,
            headers: {
                'Content-Type': 'application/json;charset=utf-8'
            }
        })
            .then(res => {
                updateAllEmployees(res.data)
            })
    }

    useEffect(() => {
        document.querySelectorAll('.ant-table-cell')[4].style.width = '300px'
    })
    useEffect(() => {
        update()
    }, [])

    let accounts = data.map(item => {
        let {_id, accountType, workHours} = item

        return {
            ...item,
            key: _id,
            account_editing: item,
            work_time_base: {
                workHours,
                accountType,
                _id
            }
        }
    })

    return (
        <Box2>
            <Table
                pagination={false}
                dataSource={accounts}
                columns={[...Columns()]}
                scroll={{x: 0}}
            />
        </Box2>
    )
}

const mapStateToProps = (state) => {
    return {
        data: state.all_employees_data
    }
}
const mapDispatchToProps = {
    updateAllEmployees
}

export default connect(mapStateToProps, mapDispatchToProps)(AllEmployees)