import React, {useState, useEffect} from "react"
import {Layout, Breadcrumb, Card, Col, Row, List, Skeleton} from "antd"
import styled from '@emotion/styled'
import {
    DeleteOutlined
} from '@ant-design/icons'
import {connect} from "react-redux";
import {mapDispathToProps, mapStateToProps} from "../app/app.logics";
import Url from './../../url/url.js'
import ModalRoleCreation from './modal-role-creation/modal-role-creation.js'
import {getActionPermissions, getDataPermissions} from './get-permissions-list/get-permissions-list.js'
import ModalRoleEdit from "./modal-role-edit/modal-role-edit";
import {getRole} from "./server-request-role";

const {Content} = Layout;
const axios = require('axios'); // AJAX

const RoleCreation = ({Roles, addRoles}) => {

    const [SkeletonShow, setSkeletonShow] = useState(false)

    const deleteRole = (_id) => {
        setSkeletonShow(true)

        axios({
            method: 'Delete',
            url: `${Url}/AdminPanel/Roles/${_id}`,
            headers: {
                'Content-Type': 'application/json;charset=utf-8'
            }
        })
            .then(res => {
                getRole(setSkeletonShow)
                // setSkeletonShow(false)
            })
            .catch(error => {
                if (error.response) {
                    setSkeletonShow(false)
                }
            })
    }

    useEffect(() => {
        getRole(addRoles, setSkeletonShow)
    }, [])

    let ColRole = Roles.map(role => {
        let {name, actionPermissions, dataPermissions, _id} = role
        if (!SkeletonShow) {

            let actionPermissionsItem = getActionPermissions(actionPermissions),
                dataPermissionsItem = getDataPermissions(dataPermissions)

            return (
                <Col key={_id} span={8} style={{position: 'relative'}}>
                    <Card title={name} bordered={true}>
                        <ModalRoleEdit Roles={Roles} setRoles={addRoles} setSkeletonShow={setSkeletonShow} name_role={name} role={role}/>
                        <DeleteOutlined
                            style={{
                                color: 'red',
                                fontSize: '20px',
                                position: 'absolute',
                                top: '19px',
                                right: '22px'
                            }}
                            onClick={() => {
                                deleteRole(_id)
                            }}
                        />
                        <List
                            size="small"
                            bordered
                        >
                            <List.Item style={{fontSize: "16px", fontWeight: "500"}}>
                                Ð Ð°Ð·ÑÐµÑÑÐ½Ð½ÑÐµ Ð´ÐµÐ¹ÑÑÐ²Ð¸Ñ:
                            </List.Item>
                            {actionPermissionsItem}
                            <List.Item style={{fontSize: "16px", fontWeight: "500"}}>
                                ÐÐ¾Ð¶Ð½Ð¾ Ð²Ð¸Ð´ÐµÑÑ:
                            </List.Item>
                            {dataPermissionsItem}
                        </List>
                    </Card>
                </Col>
            )
        } else {
            return <Skeleton key={_id} active/>
        }
    })

    const Box = styled.div({
        '.ant-col': {
            padding: '10px'
        },
        '.Possibilities': {
            fontWeight: '600',
            fontSize: '15px'
        },
        '.ant-list-bordered': {
            border: 'none'
        },
        '.ant-card-head': {
            backgroundColor: '#e6696952'
        }
    })

    return (
        <Content style={{padding: "0 50px"}}>
            <Breadcrumb style={{margin: "16px 0"}}>
                <Breadcrumb.Item>ÐÐ´Ð¼Ð¸Ð½ Ð¿Ð°Ð½ÐµÐ»Ñ</Breadcrumb.Item>
                <Breadcrumb.Item>
                    Ð¡Ð¾Ð·Ð´Ð°Ð½Ð¸Ðµ ÑÐ¾Ð»ÐµÐ¹
                    <ModalRoleCreation dataRole={Roles} setDataRole={addRoles} setSkeletonShow={setSkeletonShow}/>
                </Breadcrumb.Item>
            </Breadcrumb>
            <Box className="site-layout-content">
                <div className="site-card-wrapper">
                    <Row gutter={16}>
                        {ColRole}
                    </Row>
                </div>
            </Box>
        </Content>
    );
};

export default connect(mapStateToProps, mapDispathToProps)(RoleCreation);