const initialState = {
    all_employees_data: [],
    roles: [],
    loading_button: false
}

const reducer = (state = initialState, action) => {
    console.log(action.type)
    
    const {type, payload} = action
    
    switch (type) {
        case 'ALL_EMPLOYEES_DATA':
            return {
                ...state,
                all_employees_data: payload
            }
        case 'ADD_ROLES':
            return {
                ...state,
                roles: payload
            }
        case 'EDIT_ACCOUNT':
            let new_data = state.all_employees_data.map((account) => {
                if (account._id !== payload._id) {
                    return account
                } else {
                    return payload
                }
            })
            return {
                ...state,
                all_employees_data: new_data
            }
        case 'SET_BUTTON_STATE':
            return {
                ...state,
                loading_button: !state.loading_button
            }
        default:
            return state
    }
}

export default reducer