import {RestrictionMessage} from '../../alert/alert'
import localStorage_change from './../../#localStorage_change';
import {swallErr} from "../../alert/alert";

const errorHandler = (onFunction, error, ...additionalActions) => {
    if (error.response) {
        additionalActions.forEach(fnc => {fnc()})
        RestrictionMessage(error.response.status)
        let {status, data} = error.response;

        if (data.message === 'Bad Request') {
            swallErr('ÐÐµ Ð²Ð¾Ð·Ð¼Ð¾Ð¶Ð½Ð¾ ÑÐ´Ð°Ð»Ð¸ÑÑ Ð²Ð¾ÑÐ¾Ð½ÐºÑ Ð² ÐºÐ¾ÑÐ¾ÑÐ¾Ð¹ ÐµÑÑÑ ÑÑÐµÐ½Ð¸Ðº');
        } else if (status == 404) {
            swallErr('Ð¡ÐµÑÐ²ÐµÑ Ð½Ðµ Ð½Ð°Ð¹Ð´ÐµÐ½');
        } else if (status == 401) {

            if (data.message == 'TOKEN_EXPIRED') {

                localStorage_change(data.token);
                onFunction();
            } else {
                localStorage.removeItem('tokenID');
                window.location.replace("/");
            }
        } else if (status == 500) {
            swallErr('ÐÑÐ¸Ð±ÐºÐ° ÑÐµÑÐ²ÐµÑÐ°');
        } else if (status == 400) {
            swallErr('ÐÐµ Ð²ÐµÑÐ½Ð¾ Ð²Ð²ÐµÐ´ÐµÐ½Ñ Ð´Ð°Ð½Ð½ÑÐµ');
        }
    }
}

export default errorHandler